<template>
  <div class="register" v-wechat-title="$route.meta.title">
    <div class="register-box">
      <div class="box">
        <div class="title">立即注册</div>
        <div class="form">
          <el-input v-model="mobile" placeholder="手机号"></el-input>
          <div class="sendcode">
            <el-input
              placeholder="短信验证码"
              class="code"
              v-model="code">
            </el-input>
            <el-button type="primary" v-if="sendAuthCode" @click="getCode()">获取验证码</el-button>
            <span
              v-show="!sendAuthCode"
              class="auth_text"
            >
              <span  class="auth_text_blue await" >{{auth_time}}</span> 秒重新发送
            </span>
          </div>
          <el-input class="password" placeholder="密码" v-model="password" show-password></el-input>
          <el-input class="password" placeholder="确定密码" v-model="password2" v-on:keyup.enter="toRegister" show-password></el-input>
        </div>
        <el-button type="primary" class="register" @click="toRegister">注册</el-button>
        <div class="forget">
          <div class="agree">注册即代表同意&lt;&lt;盈链注册协议&gt; &gt;</div>
        </div>
        <div class="login"> 已有账号?
          <router-link to="loginmima">现在登录</router-link>
        </div>

      </div>
      <!-- <div class="line-box">
         <div class="tips">用其他账号登录</div>
       </div>-->
      <!--   <div class="other">
           <img class="weixin" src="@/images/weixin.png" alt="">
           <img class="weibo" src="@/images/weibo.png" alt="">
           <img class="qq" src="@/images/qq.png" alt="">
         </div>-->
    </div>
  </div>
</template>

<script>
  export default {
    name: "register", data() {
      return {
        mobile: '',
        password: '',
        password2: '',
        sendAuthCode: true,
        auth_time: "",
        code: "",
      }

    }, mounted() {

    }, methods: {
      //获取验证码
      getCode() {
        var reg = /^(13[0-9]|15[012356789]|17[0-9]|18[0-9]|14[57])[0-9]{8}$/;
        if (!this.mobile) {
          this.$message("请输入手机号码");
        } else if (!reg.test(this.mobile)) {
          this.$message("请填写有效的手机号！");
        } else if (this.mobile) {
          this.$post("/index/login/sendCaptcha", {mobile: this.mobile, type: 1})
            .then(res => {
              if (res.data.result == 200) {
                this.$message.success(res.data.msg);
                this.sendAuthCode = false;
                this.auth_time = 60;
                var auth_timetimer = setInterval(() => {
                  this.auth_time--;
                  if (this.auth_time <= 0) {
                    this.sendAuthCode = true;
                    clearInterval(auth_timetimer);
                  }
                }, 1000);
              } else {
                this.$message(res.data.msg);
              }
            });
        } else {
          this.$message("请输入手机号码");
        }
      },
      //注册
      toRegister() {
        var reg = /^(13[0-9]|15[012356789]|17[0-9]|18[0-9]|14[57])[0-9]{8}$/;
        if (!this.mobile) {
          this.$message("请输入手机号码");
        } else if (!reg.test(this.mobile)) {
          this.$message("请填写有效的手机号！");
        } else if (!this.password || !this.password2) {
          this.$message("请输入密码");
        } else if (this.password !== this.password2) {
          this.$message("密码不一致");
        } else if (!this.code) {
          this.$message("请输入验证码");
        } else {
          this.$post("index/login/register", {
            mobile: this.mobile,
            password: this.password,
            code: this.code,
          })
            .then(res => {
              if (res.data.stats) {
                this.$message({
                  message: "注册成功",
                  type: "success"
                });
                let userInfo=res.data.data;
                this.$store.commit("setLogin", userInfo); //登录状态保存在$store
                setTimeout(() => {
                  if(this.$route.query.redirect){
                    this.$router.push(this.$route.query.redirect);
                  }else {
                    this.$router.push("/portal");
                  }
                }, 1000);
              } else {
                this.$message(res.data.msg);
              }
            });
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  .auth_text{
    width: 100px;
    background-color: #ccc;
    border-radius: 4px;
    overflow: hidden;
    padding:0 10px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #99969d;

  }
  .register {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url('~@/images/bg.png') center center, 100% 100% no-repeat;

    .register-box {
      width: 734px;
      min-height: 500px;
      margin: 0 auto;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.1);
      border-radius: 6px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .box {
        width: 300px;
        margin: 0 auto;

        .title {
          margin: 36px 0;
          width: 100%;
          text-align: center;
          font-size: 30px;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
          line-height: 34px;
        }

        .form {

          .sendcode {
            margin-top: 12px;
            display: flex;
            align-items: flex-start;
            justify-content: space-between;

            .code {
              width: 150px;
            }
          }

          .password {
            margin-top: 12px;
          }


        }

        .forget {
          width: 100%;
          margin-top: 10px;
          display: flex;
          justify-content: space-between;

          .agree {
            font-size: 12px;
            font-weight: 400;
            line-height: 34px;
            color: $main;
            text-align: center;
            width: 100%;
          }

          a {
            font-size: 12px;
            font-weight: 400;
            color: $main;
            line-height: 34px;
          }
        }

        .login {
          width: 100%;
          text-align: center;
          color: rgba(51, 51, 51, 1);

          a {
            font-size: 14px;
            font-weight: 400;
            color: #C63E3E;
            line-height: 34px;
          }
        }

        .register {
          font-size: 16px;
          width: 100%;
          margin-top: 30px;
        }


      }

      .line-box {
        margin-top: 20px;
        width: 600px;
        height: 30px;
        position: relative;

        &:after {
          content: '';
          width: 100%;
          height: 1px;
          position: absolute;
          background-color: rgba(207, 207, 207, 1);;
          top: 15px;
          left: 0;
        }

        .tips {
          font-size: 14px;
          height: 100%;
          z-index: 10;
          width: 210px;
          line-height: 30px;
          text-align: center;
          background-color: #fff;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
        }
      }

      .other {
        width: 300px;
        margin-top: 38px;
        display: flex;
        justify-content: space-between;

        img {
          height: 39px;
        }

        .weixin, .weibo {
          width: 39px;
        }

        .qq {
          width: 30px;
        }
      }

    }
  }
</style>



